import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import DogHistory from '../../components/dog/history';
import LineGraph from '../../components/lineGraph';

const initialState = {};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();
    }

	async loadAll() {
		const { appState, userStore } = this.props.stores;
        appState.getFingerprint();
        await userStore.getInfo();
		await this.loadFeed();
		await this.loadWorkoutSummary();
    }

	async loadFeed() {
		const { userStore } = this.props.stores;
        await userStore.getFeed();
    }

	async loadWorkoutSummary() {
		const { userStore } = this.props.stores;
        await userStore.getWorkoutSummary();
    }

	// componentWillUnmount() {
	//     PubSub.unsubscribe(subscription);
	// }

	render() {
		const { userStore } = this.props.stores;
		const { workouts, historyFeed, workoutSummary, graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12 mb-4'>
						{/* <h5 class='mt-4'>user</h5>
						{JSON.stringify(user)} */}
						{/* <h5 class='mt-4'>team</h5>
						{JSON.stringify(team)} */}
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>
						<h5>Trenings statistikk</h5>

						<small class='mb-3'><small>
							<table class='table table-sm font-weigth-lighter'>
								<thead>
									<tr>
										<th>Mnd</th>
										{util.range(1, 12).map(w => (
											<th class='text-right' style='padding: .2rem;'>{w}</th>
										))}
									</tr>
								</thead>
								<tbody>
									<tr>
										<th scope='row' style='padding: .2rem;'>2019</th>
										{util.range(1, 12).map(m => (
											<>
												<td class='text-right' style='padding: .2rem;'>
													{userStore.findWorkoutSummaryMonth(2019, m)}<br />
													{userStore.findWorkoutSummaryMonth(2019, m, 'elevation')}<br />
												</td>
											</>
										))}
										<td class='text-muted' style='padding: .2rem;'>
											<nobr><i class='fas fa-road' /> km</nobr><br />
											<nobr><i class='fas fa-mountain' /> m</nobr><br />
										</td>
									</tr>
									<tr>
										<th scope='row' style='padding: .2rem;'>2020</th>
										{util.range(1, 12).map(m => (
											<>
												<td class='text-right' style='padding: .2rem;'>
													{userStore.findWorkoutSummaryMonth(2020, m)}<br />
													{userStore.findWorkoutSummaryMonth(2020, m, 'elevation')}<br />
												</td>
											</>
										))}
										<td class='text-muted' style='padding: .2rem;'>
											<nobr><i class='fas fa-road' /> km</nobr><br />
											<nobr><i class='fas fa-mountain' /> m</nobr><br />
										</td>
									</tr>
								</tbody>
							</table>
						</small></small>
									
						<LineGraph
							stores={this.props.stores}
							data={graphWorkoutSummaryWeekDistanceCurrent}
							dataB={graphWorkoutSummaryWeekDistancePrev}
							legend='2020'
							legendB='2019'
							width={550}
							height={150}
							yMin={0}
							showYTicks={1}
							unit='km'
							showXTicks={1}
							// avgWindow={1}
						/>
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5>Siste fra hundene</h5>
						</div>
						{historyFeed && historyFeed.map(line => (
							<DogHistory stores={this.props.stores} line={line} />
						))}
					</div>
				</div>
				{/* <div class='row'>
					<div class='col-12 mb-4'>
						<h5>Hundene - {dogs.length} stk</h5>
						<div class='row'>
							{dogs && dogs.map(dog => (
								<Dog stores={this.props.stores} dog={dog} />
							))}
						</div>
					</div>
				</div> */}
				<div class='row'>
					<div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>		
							<h5>Siste treningsturer - {workouts.length} stk</h5>
						</div>
						{workouts && workouts.map(workout => (
							<Workout stores={this.props.stores} workout={workout} />
						))}
					</div>
				</div>
				<div class='row'>
					<div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5 class='mt-4'>Treningsoversikt</h5>
						</div>
						{/* {JSON.stringify(workoutSummary)} */}
						{workoutSummary && workoutSummary.map(summary => (
							<WorkoutSummary stores={this.props.stores} summary={summary} />
						))}
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
